@import url('https://fonts.googleapis.com/css2?family=Abel&family=Play&display=swap');
*{
        margin: 0;
        padding: 0;
      }
      .main{
        height: 100vh;
        width: 100vw;
        background: #434242;
        z-index: 77;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-family: 'Abel',sans-serif
      }
      .hed{
        background: #434242;
        display: flex;
        justify-content: center;
        align-items: center;
        letter-spacing: 6px;
        word-spacing: 12px;
      }
      .hed::after{
        content: "";
        height: 36px;
        width: 80%;
        right: 10%;
        position: absolute;
        background: #434242;
        border-left: 4px solid #fff;
        animation: dkji 4s infinite steps(18);
      }
      @keyframes dkji{
        50%{
          width: 0%;
        }
        51%{
          width: 80%;
        }
        99%{
          width: 0%;
        }
        100%{
          width: 80%;
        }
      }
      #fetch{
        height: 60px;
        width: 100vw;
        border: none;
        background: #434242;
        color: #fff;
        position:fixed;
        top: 0;
        left:0;
        outline: none;
        text-align: center;
        font-family: 'Abel', sans-serif;
        font-size: 22px;
        letter-spacing: 3px;
      }
      .contener{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        top: 180px;
        height: auto;
        width: 100%;
        background: #434242;
      }
     .card{
       padding: 20px;
       height: 200px;
       width: 300px;
       display: flex;
       background: #222222;
       flex-direction: column;
       border: 2px solid #fff;
       border-radius: 20px;
       margin-bottom: 5px;
       transition: all 1s ease;
     } 
     .university{
       text-align: center;
       color: #22A39F;
       font-family: 'Abel', sans-serif;
     }
     a{
       text-decoration: none;
       padding: 10px;
       width: 80px;
       font-family: 'Abel', sans-serif;
       background: #434242;
       text-align: center;
       border-radius: 10px;
       border: none;
       color: #F3EFE0;
       position: relative;
       left: calc(50% - 50px);
       top: 40%;
       transition: all .4s ease;
     }
     p{
       color: #F3EFE0;
       position: relative;
       text-align: center;
       top: 20%;
       font-family: 'Abel', sans-serif;
     }
     a:hover{
       color:#434242 ;
       background:#F3EFE0;
       letter-spacing: .5px;
       width: 90px;
     }
     .card:hover > p{
       color: #F3CCFF;
     }
     .card:hover > .university{
       color: #FFFFD0;
       
     }
     .card:hover{
       background: black;
       box-shadow: 2px 2px 10px 3px #ddd;
     }
    @media only screen and (min-width:620px){
      .contener{
        flex-direction:row;
      }
    }
      